
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import { mapGetters } from 'vuex'
import { API, getQuery } from '../../services';

export default {
directives: {
TransferDom
},
components: {
Actionsheet,
XHeader,
Group,
Cell,
XTextarea,
Toast,
CellBox,
Popup
},
data() {
return {
payShow:false,
menus:{
menu1: '微信支付',
menu2: '支付宝支付'
},
cardstate: {
id: "",
storeId: "",
},
priceShow: false,
currentPrice: 0,
currentCard: 0,
cardShow: false,
confrim: "请选择地址",
toast: false,
toasts: false,
/* 自提点数据 */
tpl: {},
orderLogistics: '/orderLogistics/',
/* 是否拥有默认数据 */
haveDefault: false,
/* 订单id */
cartId: 0,
/* 订单详情数据 */
infoData: {},
/* 时间对照表 */
timeMap: { 1: "日", 2: "周", 3: "月", 4: "季", 5: "年" },
/* 运费规则列表 */
tplRules: {},
/* 买家留言 */
option: "",
paydata: {

},
cardList: [],
/* 支付方式 */
payMethod: 4,
payTypeData:null,
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
/* 返回物流方式 */
returnTpl() {
this.getTplPrice();
if (this.$store.state.tplId == 1) {
return "快递";
} else if (this.$store.state.tplId == 2) {
return "上门";
} else if (this.$store.state.tplId == 3) {
this.tpl = this.$store.state.sinceData;
return "自提";
} else {
return "请选择物流方式";
}
},
/* 返回初始和结束周期 */
rentRange() {
let self = this;
let start = dateFormat(new Date(self.infoData.cart_start_time * 1000), 'YYYY-MM-DD');
let end = dateFormat(new Date(self.infoData.cart_end_time * 1000), 'YYYY-MM-DD');
return { start, end };
},
/* 返回周期名称 */
/* 返回当前周期文字 */
timeText() {
return this.timeMap[this.infoData.rent_period_type];
},
/* 计算商品押金 */
goodsDespoit() {
if (this.infoData.goods_deposit == 0) {
return 0;
}
let despoite = this.infoData.goods_deposit * this.infoData.cart_content_good_amount - this.goodsAllPrice;
if (despoite <= 0) {
return 0;
} else {
return despoite;
}
},
/* 计算商品总租金 */
/* 租期*金额*数量+押金+运费 */
goodsAllPrice() {
let Price = this.infoData.rent_period_now_rent_price * this.infoData.cart_time_number * this.infoData.cart_content_good_amount;
let allPrice = Price + this.currentPrice;
return allPrice <= 0 ? 0 : allPrice;
},
/* 运费计算 */
returnTplPrice() {
let num = this.infoData.cart_content_good_amount;
let second = num - 1;
if (this.$store.state.tplId == 1) {
return this.tplRules.freight_first_price + this.tplRules.freight_continued_price * second - 0;
} else if (this.$store.state.tplId == 2) {
return this.tplRules.freight_door_cost * num - 0;
} else if (this.$store.state.tplId == 3) {
this.tpl = this.$store.state.sinceData;
return 0;
} else {
return 0;
}
}
},
methods: {
browserPay(key){
if(key=='menu1'){
/* 微信支付 */
this.payMethod=5;
}else if(key=='menu2'){
this.payMethod=4;
}else{
return false;
}
API.order.orderPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
info: [{
addressId: this.$store.state.tplId != 3 ? this.$store.state.addressData.id : "",//地址id 物流或者上门选择该字段
cartId: this.cartId, //购物车id
cart_tpl: this.$store.state.tplId,//物流方式
freight_continued_price: this.tplRules.freight_continued_price,//快递续件运费
freight_door_cost: this.tplRules.freight_door_cost,//上门的运费
freight_first_price: this.tplRules.freight_first_price,//快递的收件运费
goodsAmount: this.infoData.cart_content_good_amount,//数量
order_time_number: this.infoData.cart_time_number,//租赁周期
rentTime: this.infoData.cart_start_time,                    rent_period_id: this.infoData.cart_content_rent_prieod_id,                    returnTime: this.infoData.cart_end_time,                    sinceId: this.$store.state.tplId == 3 ? this.$store.state.sinceData.since_id : "",//自提点id
coupon_no: this.cardstate.id,
message: this.option,
}]
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
self.payTypeData=resopndy.body;
if(key=='menu1'){
/* 微信支付 */
window.location.href=this.payTypeData;
}else if(key=='menu2'){
const div = document.createElement('div');
div.innerHTML = this.payTypeData;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}
}, (err) => {
self.confrim = "支付异常";
self.toast = true;
self.$router.push({
path: '/index/main/order'
})
});
}
});

},
/* 判断当前；浏览器环境  0 微信 1 支付宝 2 其他浏览器*/
isAlipay() {
var userAgent = navigator.userAgent.toLowerCase();
if (userAgent.match(/Alipay/i) == "alipay") {
this.payMethod = 4;
return 1;
} else if (userAgent.match(/MicroMessenger/i) == "micromessenger") {
this.payMethod = 3;
return 0;
} else {
return 2;

}
},
goShop(item) {
this.cardstate.id = item.coupon_no;
this.cardstate.storeId = item.store_id;
this.currentPrice = 0 - item.amount;
this.priceShow = true;
this.cardShow = false;
},
closeCard() {
this.cardShow = false;
},
/* 支付接口 */
cardshow() {
if (this.cardList.length == 0) {
this.confrim = "暂无可用红包";
this.toast = true;
return false;
}
this.cardShow = true;
},
onBridgeReady() {
let self = this;
WeixinJSBridge.invoke(
'getBrandWCPayRequest', self.paydata,
function(res) {
localStorage.setItem("reload", "1");
if (res.err_msg == "get_brand_wcpay_request:fail") {
self.confrim = "支付异常";
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/index/main/order'
})

}, 500);
}
if (res.err_msg == "get_brand_wcpay_request:cancel") {
self.$router.push({
path: '/index/main/order'
})

}
if (res.err_msg == "get_brand_wcpay_request:ok") {
self.confrim = "支付成功";
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/index/main/order'
})

}, 500);
}     // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
}
);
},
routerBack() {
this.$router.goBack();
},
/* 提交订单 */
buygoods() {

/* 数值校验 */
if (this.getAddress == "") {
this.confrim = "请选择地址";
this.toasts = true;
return false;
}
if (this.returnTpl == "请选择物流方式") {
this.confrim = "请选择物流方式";
this.toasts = true;
return false;
}
let self=this;
if(this.isAlipay()==0){
API.order.orderPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
info: [{
addressId: this.$store.state.tplId != 3 ? this.$store.state.addressData.id : "",//地址id 物流或者上门选择该字段
cartId: this.cartId, //购物车id
cart_tpl: this.$store.state.tplId,//物流方式
freight_continued_price: this.tplRules.freight_continued_price,//快递续件运费
freight_door_cost: this.tplRules.freight_door_cost,//上门的运费
freight_first_price: this.tplRules.freight_first_price,//快递的收件运费
goodsAmount: this.infoData.cart_content_good_amount,//数量
order_time_number: this.infoData.cart_time_number,//租赁周期
rentTime: this.infoData.cart_start_time,                    rent_period_id: this.infoData.cart_content_rent_prieod_id,                    returnTime: this.infoData.cart_end_time,                    sinceId: this.$store.state.tplId == 3 ? this.$store.state.sinceData.since_id : "",//自提点id
coupon_no: this.cardstate.id,
message: this.option,
}]
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
/* 微信支付 */
this.paydata = resopndy.body;
if (typeof WeixinJSBridge == "undefined") {
if (document.addEventListener) {
document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady, false);
} else if (document.attachEvent) {
document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady);
}
} else {
self.onBridgeReady();
}
}, (err) => {
self.confrim = "支付异常";
self.toast = true;
self.$router.push({
path: '/index/main/order'
})
});
}
});
}else if(this.isAlipay()==1){
API.order.orderPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
info: [{
addressId: this.$store.state.tplId != 3 ? this.$store.state.addressData.id : "",//地址id 物流或者上门选择该字段
cartId: this.cartId, //购物车id
cart_tpl: this.$store.state.tplId,//物流方式
freight_continued_price: this.tplRules.freight_continued_price,//快递续件运费
freight_door_cost: this.tplRules.freight_door_cost,//上门的运费
freight_first_price: this.tplRules.freight_first_price,//快递的收件运费
goodsAmount: this.infoData.cart_content_good_amount,//数量
order_time_number: this.infoData.cart_time_number,//租赁周期
rentTime: this.infoData.cart_start_time,                    rent_period_id: this.infoData.cart_content_rent_prieod_id,                    returnTime: this.infoData.cart_end_time,                    sinceId: this.$store.state.tplId == 3 ? this.$store.state.sinceData.since_id : "",//自提点id
coupon_no: this.cardstate.id,
message: this.option,
}]
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
const div = document.createElement('div');
div.innerHTML = resopndy.body;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}, (err) => {
self.confrim = "支付异常";
self.toast = true;
self.$router.push({
path: '/index/main/order'
})
});
}
});
}else{
this.payShow=true;
}

},
/* 数据初始化 */
Initialization() {
this.cartId = this.$route.params.id;
API.order.orderConfirm({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
cartId: [this.cartId],
}).then((res) => {
this.infoData = res.body.data.cart_list[0];
this.orderLogistics = "/orderLogistics/" + this.infoData.cart_content_goods_id;
API.card.storeCard({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
storeId: this.infoData.store_id,
}).then((res) => {
if (res.body.code == 200) {
this.cardList = res.body.data;
}
});
});
this.getDefaultAddress();


/* 清除之前的快递信息 */
this.$store.dispatch('ClearTpl');
},
/* 获取用户默认地址，并且出发vuex更新 */
getDefaultAddress() {
API.person.addressInfo({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
is_default: 1
}).then((res) => {
if (res.body.code == 200) {
this.haveDefault = true;
res = res.body.data.addressList;
this.$store.dispatch('SetAddress', res);
} else {
this.haveDefault = false;
}
});
},
/* 获取运费模板 */
getTplPrice() {
if (this.$store.state.addressData.detail != "") {
API.order.tplPrice({
tpl: this.$store.state.tplId,
province: this.$store.state.addressData.province,
city: this.$store.state.addressData.city,
region: this.$store.state.addressData.district,
goodsId: this.infoData.cart_content_goods_id
}).then((res) => {
if (res.body.code == 200) {
this.tplRules = res.body.data;

}
});
}
}
}, mounted() {
overscroll(document.querySelector('.orderInfo_main_content'));
/* 获取总数据 */
this.Initialization();
},
activated() {
overscroll(document.querySelector('.orderInfo_main_content'));
/* 新订单时触发更新 */
if (localStorage.getItem('orderClick')) {
localStorage.setItem('orderClick', '');
this.Initialization();
this.cardstate = {
id: "",
storeId: "",
};
this.priceShow = false;
this.currentPrice = 0;
}
/* 地址点击函数 */
if (localStorage.getItem('addressClick')) {
localStorage.setItem('addressClick', '');
this.haveDefault = true;
}
}
}



